.images {
    position: relative;
    display: flex;

    img {
        width: 300px;
        border-radius: 40px;
        position: relative;
        filter: brightness(80%);
        animation-name: floating;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
    }

    img:nth-child(1) {
        z-index: 7;
        left: 80px;
    }

    img:nth-child(2) {
        z-index: 8;
        top: 80px;
    }

    img:nth-child(3) {
        z-index: 9;
        right: 60px;
    }

    img:nth-child(4) {
        z-index: 10;
        top: 80px;
        right: 100px;
    }

    @keyframes floating {
        0% {
            transform: translate(0, 0px);
        }

        50% {
            transform: translate(0, 15px);
        }

        100% {
            transform: translate(0, -0px);
        }
    }
}

.label {
    position: absolute;
    z-index: 11;

    div:nth-child(1) {
        position: relative;
        font-size: 90px;
        left: 286px;
        top: 90px;
        letter-spacing: -5px;
    }

    div:nth-child(2) {
        position: relative;
        left: 556px;
        font-size: 110px;
        color: #FFF38A;
        top: 20px;
        letter-spacing: -5px;
    }
}